import React, {useState, useEffect, useRef} from 'react';
import "./checkout.css";
import AccountBalanceWalletIcon from '@material-ui/icons/AccountBalanceWallet';
import PhoneIphoneIcon from '@material-ui/icons/PhoneIphone';
import { Link } from 'react-router-dom';

const Checkout = () => {

    const nameRef = useRef(null);
    const addressRef = useRef(null);
    const countryRef = useRef(null);
    const cityRef = useRef(null);
    const stateRef = useRef(null);
    const pincodeRef = useRef(null);
    const mailRef = useRef(null);
    const phoneRef = useRef(null);
    const submitRef = useRef(null);
    
    useEffect(() => {
        nameRef.current.focus();
    }, [])

    const [country, setCountry] = useState("India");

    function handleClick1(event) {
        let newCountry = event.target.value;
        setCountry(newCountry);
    }

    const [payment, setPayment] = useState("COD");

    function handleClick2(event) {
        let newPayment = event.target.value;
        setPayment(newPayment)
    }

    function onPageLoad() {

        document.getElementById("stateSelect").selectedIndex = 0;
        document.getElementById("citySelect").selectedIndex = 0;

    }

        // -------------------------------------------------- Create Order API --------------------------------------------------------------

        function addVars(event) {

            // var myHeaders = new Headers();
            // let newToken = localStorage.getItem("TOKEN");
            // myHeaders.append("Content-Type", "application/json");
            // myHeaders.append("Authorization", "Bearer " + newToken);

            let srName = String(nameRef.current.value);
            let srAddress = String(addressRef.current.value);
            let srCity = String(cityRef.current.value);
            let srState = String(stateRef.current.value);
            let srCountry = String(country);
            let srPincode = Number(pincodeRef.current.value);
            let srEmail  = String(mailRef.current.value);
            let srPhone  = String(phoneRef.current.value);

            localStorage.setItem("srName", srName);
            localStorage.setItem("srAddress", srAddress);
            localStorage.setItem("srCity", srCity);
            localStorage.setItem("srState", srState);
            localStorage.setItem("srCountry", srCountry);
            localStorage.setItem("srPincode", srPincode);
            localStorage.setItem("srEmail", srEmail);
            localStorage.setItem("srPhone", srPhone);
            localStorage.setItem("srPayment", payment);

            // var raw = JSON.stringify({"order_id":"123-141","order_date":srDate,"pickup_location":"Primary",
            //                         "address":"Bengaluru, Karnataka, India","address_2":"JP Nagar, 8th Phase","city":"Bangalore",
            //                         "billing_customer_name":srName, "billing_last_name": "", "billing_address":srAddress,
            //                         "billing_city":srCity,"billing_pincode":srPincode,"billing_state":srState,
            //                         "billing_country":srCountry,"billing_email":srEmail,"billing_phone":srPhone,
            //                         "shipping_is_billing":true,"order_items":[...newArray],
            //                         "payment_method":"Prepaid","sub_total":srTotal,"length":10,"breadth":15,"height":20,"weight":2.5});

            // var requestOptions = {
            // method: 'POST',
            // headers: myHeaders,
            // body: raw,
            // redirect: 'follow'
            // };

            // fetch("https://apiv2.shiprocket.in/v1/external/orders/create/adhoc", requestOptions)
            // .then(response => response.text())
            // .then(result => console.log(result))
            // .catch(error => console.log('error', error));

            event.preventDefault();
        }

        function keydown1(e) {
            if(e.key === "Enter") {
                mailRef.current.focus();
            }
        }

        function keydown2(e) {
            if(e.key === "Enter") {
                phoneRef.current.focus();
            }
        }

        function keydown3(e) {
            if(e.key === "Enter") {
                countryRef.current.focus();
            }
        }

        function keydown4(e) {
            if(e.key === "Enter") {
                stateRef.current.focus();
            }
        }

        function keydown5(e) {
            if(e.key === "Enter") {
                cityRef.current.focus();
            }
        }

        function keydown6(e) {
            if(e.key === "Enter") {
                pincodeRef.current.focus();
            }
        }

        function keydown7(e) {
            if(e.key === "Enter") {
                addressRef.current.focus();
            }
        }

        function keydown8(e) {
            if(e.key === "Enter") {
                submitRef.current.focus();
            }
        }

        var citiesByState = {
                "Andhra Pradesh": ["Visakhapatnam", "Vijayawada", "Guntur", "Nellore", "Kurnool", "Kakinada", "Rajamahendravaram", "Kadapa", 
                                    "Tirupati", "Anantapuram", "Vizianagaram", "Eluru", "Chirala", "Ongole", "Adoni", "Madanapalle",
                                    "Machilipatnam", "Tenali", "Proddatur", "Chittoor", "Hindupur", "Srikakulam", "Bhimavaram", "Guntakal",
                                    "Dharmavaram", "Gudivada", "Narasaraopet", "Tadipatri", "Mangalagiri", "Tadepalligudem", "Amaravati", "Chilakaluripet"],
            
                "Goa": ["Bicholim", "Canacona", "Cuncolim", "Curchorem", "Mapusa", "Margao", "Mormugao", "Panaji", "Pernem", "Ponda", "Quepem",
                        "Sanguem", "Sanquelim", "Valpoi"],
            
                "Gujarat": ["Ahmedabad", "Surat", "Vadodara", "Rajkot", "Bhavnagar", "Jamnagar", "Junagadh", "Gandhinagar", "Gandhidham", "Anand", "Navsari",
                            "Morbi", "Nadiad", "Surendranagar", "Bharuch", "Mehsana", "Bhuj", "Daman", "Porbandar", "Palanpur", "Valsad", "Vapi", "Gondal", 
                            "Veraval", "Godhara", "Patan", "Kalol, Gandhinagar", "Dahod", "Botad", "Amreli", "Deesa", "Jetpur"],
            
                "Karnataka": ["Bengaluru", "Hubli-Dharwad", "Mysuru", "Kalaburagi", "Mangaluru", "Belagavi", "Davanagere", "Ballari", "Vijayapura", "Shivamogga",
                            "Tumakuru", "Raichur", "Bidar", "Hosapete", "Gadag-Betageri", "Robertsonpete", "Hassan", "Bhadravati", "Chitradurga", "Udupi", "Kolara", 
                            "Mandya", "Chikkamagaluru", "Gangavati", "Bagalkote", "Ranebennuru"],
                
                "Kerala": ["Alleppey", "Ernakulam", "Idukki", "Kannur", "Kasaragod", "Kollam", "Kottayam", "Kozhikode", "Malappuram", "Munnar", "Palakkad",
                            "Thekkady", "Thiruvananthapuram", "Thrissur", "Wayanad", "Varkala"],

                "Maharashtra": ["Mumbai", "Pune", "Nagpur", "Nashik", "Thane", "Aurangabad", "Solapur", "Dhule", "Amaravati", "Malegoan", "Kolhapur",
                                "Nanded-Waghela", "Sangli", "Bhiwandi-Nizampur", "Akola", "Latur", "Jalgaon", "Ahmednagar", "Miraj", "Ichalkaranji",
                                "Chandrapur", "Parbhani", "Jalna", "Bhusawal", "Navi Mumbai Panvel Raigad", "Panvel City", "Satara", "Beed", "Yavatmal",
                                "Kamptee", "Gondia", "Barshi", "Achalpur", "Osmanabad", "Nandurbar", "Wardha", "Udgir", "Hinganghat"],

                "Punjab": ["Ludhiana", "Amritsar", "Jalandhar", "Patiala", "Bathinda", "Hoshiarpur",
                            "Hoshiarpur", "Mohali", "Batala", "Pathankot", "Moga", "Abohar", "Malerkotla",
                            "Khanna", "Phagwara", "Muktsar", "Barnala", "Rajpura", "Firozpur", "Kapurthala", "Sunam"],

                "Rajasthan": ["Jaipur", "Jodhpur", "Ajmer", "Udaipur", "Kota", "Bikaner", "Bhilwara",
                                "Alwar", "Bharatpur", "Sri Ganganagar", "Sikar", "Pali", "Hanumangarh"],

                "Tamil Nadu": ["Chennai", "Coimbatore", "Madurai", "Tiruchirappalli", "Tiruppur", "Salem", 
                                "Erode", "Tirunelveli", "Vellore", "Thoothukkudi", "Dindigul", "Thanjavur",
                                "Ranipet", "Sivakasi", "Karur", "Udhagamandalam", "Hosur", "Nagercoil", 
                                "Kancheepuram", "Kumarapalayam", "Karaikkudi", "Neyveli", "Cuddalore", "Kumbakonam", 
                                "Tiruvannamalai", "Pollachi", "Rajapalayam", "Gudiyatham", "Pudukkottai", "Vaniyambadi", 
                                "Ambur", "Nagapattinam"],

                "Telangana": ["Hyderabad", "Warangal", "Mahabubnagar", "Khammam", "Ramagundam", "Nizamabad",
                            "Suryapet", "Nalgonda", "Miryalaguda", "Karimnagar", "Adilabad", "Jagityal", "Nirmal"],
                            
                "West Bengal": ["Kolkata", "Asansol", "Siliguri", "Durgapur", "Bardhaman", "English Bazar", 
                                "Baharampur", "Habra", "Kharagpur", "Shantipur", "Dankuni", "Dhulian", 
                                "Ranaghat", "Haldia", "Raiganj", "Krishnanagar", "Nabadwip", "Medinipur", 
                                "Jalpaiguri", "Balurghat", "Basirhat", "Bankura", "Chakdaha", "Darjeeling", 
                                "Alipurduar", "Purulia", "Jangipur", "Bangaon", "Cooch Behar"]
                            }
            function makeSubmenu(value) {
            
                if(value.length===0) document.getElementById("citySelect").innerHTML = "<option></option>";
                else {
                    var citiesOptions = "";
                    var cityId;
                    for(cityId in citiesByState[value]) {
                    citiesOptions+="<option>"+citiesByState[value][cityId]+"</option>";
                    }
                    document.getElementById("citySelect").innerHTML = citiesOptions;
                }
            };  

    return (
        <div onLoad={onPageLoad} className="container contact ss-item-required">

            <h4>Order Now</h4>

            <form className="text-center">

                <input name="name" onKeyDown={keydown1} type="text" id="defaultContactFormName" ref={nameRef} className="form-control mb-4 border-required" placeholder="Name" />

                <input name="email" onKeyDown={keydown2} type="email" id="defaultContactFormEmail" ref={mailRef} className="form-control mb-4 border-required" placeholder="E-mail" />

                {country==="India" && <input onKeyDown={keydown3} placeholder="10-Digit Contact Number" id="defaultContactFormPhone" ref={phoneRef} className="border-required phone" type="" name="phone" pattern="[0-9]{10}" maxLength="10" />}

                {country==="Other" && <input onKeyDown={keydown3} placeholder="Contact Number with Country Code" id="defaultContactFormPhone1" red={phoneRef} className="border-required phone" type="number" name="phone" />}
                
                <div className="locationInput">

                <select name="country" onKeyDown={keydown4} onChange={handleClick1} ref={countryRef} id="countrySelect" className="locationInputs">
                    <option value="India">India</option>
                    <option value="Other">Other</option>
                    
                </select>
                
                {country === "India" && <select onKeyDown={keydown5} name="state" ref={stateRef} defaultValue="State" className="locationInputs" id="stateSelect" size="1" onChange={event => makeSubmenu(event.target.value)}>
                    <option className="states" disabled>State</option>
                    <option className="states">Andhra Pradesh</option>
                    <option className="states">Goa</option>
                    <option className="states">Gujarat</option>
                    <option className="states">Karnataka</option>
                    <option className="states">Kerala</option>
                    <option className="states">Maharashtra</option>
                    <option className="states">Punjab</option>
                    <option className="states">Rajasthan</option>
                    <option className="states">Tamil Nadu</option>
                    <option className="states">Telangana</option>
                    <option className="states">West Bengal</option>
                </select>}
            
                </div>

                <div className="locationInput">
                {country === "India" && <select name="city" onKeyDown={keydown6} defaultValue="City" className="locationInputs" ref={cityRef} id="citySelect" size="1" >
                    <option disabled>City</option>
                    <option></option>
                </select>}
               
                {country === "India" && <input onKeyDown={keydown7} placeholder="PINCODE" id="defaultContactFormPin" ref={pincodeRef} className="border-required pincode locationInputs" type="" name="pin" pattern="[0-9]{6}" maxLength="6" />}


                </div>

                <input type="text" name="address" onKeyDown={keydown8} id="defaultContactFormAddress" ref={addressRef} className="form-control mb-4 border-required address" placeholder="Your Residential Address" />

                {country === "Other" && <p className="international_shipping_note">Note : Please mention the country, state and city in the address field in case you want the product to be shipped out of India.</p>}

                <div className="payment_left">
                
                    <div className="payment_options">
                    <div className="form-check form-check-inline payment_option">
                    <input className="form-check-input check_input" onChange={handleClick2} type="radio" name="payment" id="inlineRadio1" value="COD" />
                    <label className="form-check-label input_label" htmlFor="inlineRadio1"><AccountBalanceWalletIcon /> - Cash On Delivery</label>
                    </div>
                    <div className="form-check form-check-inline payment_option">
                    <input className="form-check-input check_input" onChange={handleClick2} type="radio" name="payment" id="inlineRadio2" value="Online" />
                    <label className="form-check-label input_label" htmlFor="inlineRadio2"><PhoneIphoneIcon /> - Google Pay / PhonePe / Paytm</label>
                    </div>
                    </div>
                    {payment === "Online" && <p className="gpay_order">Your order will be dispatched once you complete the online payment towards :-<br /><br /> AIGIRI FOODS - +91 9164187714<br /><br />GRAND TOTAL will be displayed in the invoice.</p>}
                
                </div>

                <Link to="/invoice"><button onMouseDown={addVars} ref={submitRef} type="submit" className="contact-btn">CONTINUE</button></Link>
                 
            </form>

        </div>
    );
}

export default Checkout;